import React from 'react';
import { Stage, Image } from 'react-konva';

// used for setting canvas size and image
class MainCanvas extends React.Component<{ image: HTMLImageElement, src: string, setWindowWidth: any }> {
    state = {
        image: new window.Image(),
        src: "",
        canvasRef: React.createRef<typeof Stage>()
    };

    componentDidUpdate(oldProps: any) {
        if (this.state.src !== this.props.src) {
            this.loadImage();
        }
    }

    setImg(src: string) {
        this.state.image.src = src
        this.state.image.addEventListener('load', this.handleLoad);
    }

    handleLoad = () => {
        this.setState({
            image: this.state.image,
        });
    };

    componentDidMount() {
        this.loadImage();
    }

    loadImage() {
        this.state.image = new window.Image();
        this.state.image.src = this.props.src;
        this.state.image.addEventListener('load', this.handleLoad);
    }

    render() {

        return (
            <Image
                x={0}
                y={0}
                image={this.state.image}
            />
        )
    }
}

export default MainCanvas;