export interface ShapeArrInterface {
    id: string,
    shape: ShapeEnum,
    data: RectDataInterface | EllipseDataInterface | PolygonDataInterface | LabelDataInterface
}

export enum ShapeEnum {
    Rectangle,
    Ellipse,
    Polygon,
    Label
}

export interface RectDataInterface {
    id: string,
    x: number,
    y: number,
    width: number,
    height: number,
    fill: string,
    isSquare: boolean,
}

export interface RectangleInterface {
    shapeProps: RectDataInterface,
    isSelected: Boolean,
    onSelect: any,
    onChange: any
}

export interface EllipseDataInterface {
    id: string,
    x: number,
    y: number,
    radiusX: number,
    radiusY: number,
    fill: string,
    isCircle: boolean,
}
export interface EllipseInterface {
    shapeProps: EllipseDataInterface,
    isSelected: Boolean,
    onSelect: any,
    onChange: any,
}

export interface PolygonDataInterface {
    id: string,
    x: number,
    y: number,
    sides: number,
    radius: number,
    fill: string,
}
export interface PolygonInterface {
    shapeProps: PolygonDataInterface,
    isSelected: Boolean,
    onSelect: any,
    onChange: any,
}

export interface LabelInterface {
    shapeProps: LabelDataInterface,
    isSelected: Boolean,
    onSelect: any,
    onChange: any,
}
export interface LabelDataInterface {
    id: string,
    x: number,
    y: number,
    text: string,
    fill: string,
}

export interface ColorInterface {
    name: string,
    hex: string,
}   