import React, { ChangeEvent, useState } from 'react';
import '../App.css';
import { Stage, Layer } from 'react-konva';
import Konva from 'konva';
import MainCanvas from './MainCanvas';
import { downloadURI, isNum, isColor, isRectangle, isEllipse, isPolygon, isLabel } from './generalFunction'
import { KonvaEventObject } from 'konva/lib/Node';
import { RectDataInterface, EllipseDataInterface, PolygonDataInterface, ShapeEnum, ShapeArrInterface, LabelDataInterface } from './Interface'
import Rectangle from './Rectangle'
import EllipseC from './Ellipse';
import Polygon from './Polygon';
import LabelC from './Label';

function App() {
  const [fileName, setFile] = useState("");
  const [imgWidth, setImgWidth] = useState(0);
  const [imgHeight, setImgHeight] = useState(0);
  const [src, setSrc] = useState("");
  const stageRef = React.useRef<Konva.Stage>(null)
  // shape
  const [shapeId, setShapeId] = useState(0);
  const [selectedId, selectShape] = useState("");
  const [selectedColor, selectColor] = useState("");
  const [shapeArr, setShapeArr] = useState(new Array<ShapeArrInterface>());
  // Rectagle
  const [isSquare, setIsSqure] = useState(false);
  const [rectX, setRectX] = useState("0");
  const [rectY, setRectY] = useState("0");
  const [rectWidth, setRectWidth] = useState("");
  const [rectHeight, setRectHeight] = useState("");
  const [rectFill, setRectFill] = useState("");
  // Circle
  const [isCircle, setIsCircle] = useState(false);
  const [ellipseX, setEllipseX] = useState("0");
  const [ellipseY, setEllipseY] = useState("0");
  const [ellipseRadiusX, setEllipseRadiusX] = useState("");
  const [ellipseRadiusY, setEllipseRadiusY] = useState("");
  const [ellipseFill, setEllipseFill] = useState("");
  // Polygon  
  const [polygonX, setPolygonX] = useState("0");
  const [polygonY, setPolygonY] = useState("0");
  const [polygonSides, setPolygonSides] = useState("");
  const [polygonRadius, setPolygonRadius] = useState("");
  const [polygonFill, setPolygonFill] = useState("");
  // Label    
  const [labelX, setLabelX] = useState("0");
  const [labelY, setLabelY] = useState("0");
  const [labelText, setLabelText] = useState("");
  const [labelFill, setLabelFill] = useState("");
  const [selectedLabelText, setSelectedLabelText] = useState("");

  // import and display image
  const handleSetImage = (event: ChangeEvent<HTMLInputElement>) => {
    const { files } = event.target;
    reader(files).then(result => setImg("" + result));
  }
  const reader = (files: FileList | null) => {
    return new Promise((resolve, reject) => {
      if (FileReader && files && files.length) {
        const fileReader = new FileReader();
        fileReader.onload = () => resolve(fileReader.result);
        fileReader.readAsDataURL(files[0]);
        setFile(files[0].name);
      }
    });
  }
  const setImg = (result: string) => {
    setSrc(result);
    const img = new window.Image();
    img.addEventListener("load", function () {
      setImgWidth(this.naturalWidth);
      setImgHeight(this.naturalHeight);
    });
    img.src = result;
  }

  // export image
  const handleExport = () => {
    selectShape("");
    // timeout used to wait for selected shaped to be deselected
    setTimeout(() => {
      if (stageRef !== null && stageRef.current !== null) {
        const uri = stageRef.current.toDataURL();
        downloadURI(uri, fileName);
      }
    }, 100);
  };

  const checkDeselect = (e: KonvaEventObject<MouseEvent | TouchEvent>) => {
    if (e.target.className !== "Text") {
      setSelectedLabelText("");
    }
    if (e.target.className === "Image") {
      selectShape("");
    }
  };

  // add rectangle
  const handleSubmitRect = (event: ChangeEvent<HTMLFormElement>) => {
    event.preventDefault();
    if (!isNum(rectX) && !isNum(rectY)) {
      alert("Rectangle X and Y position must be valid integer number");
      return;
    }
    if (!isNum(rectHeight) && (isSquare || !isNum(rectWidth))) {
      alert("Rectangle height and width must be valid integer number");
      return;
    }
    if (!isColor(rectFill)) {
      alert("color must be valid color, use #xxyyzz for hex color codes");
      return;
    }
    const id = "shapeId_" + shapeId
    shapeArr.push({
      id: id,
      shape: ShapeEnum.Rectangle,
      data: {
        x: parseInt(rectX),
        y: parseInt(rectY),
        width: parseInt(rectWidth),
        height: isSquare ? parseInt(rectWidth) : parseInt(rectHeight),
        fill: rectFill,
        id: id,
        isSquare: isSquare,
      }
    });
    setShapeArr(shapeArr);
    setRectHeight("");
    setRectWidth("");
    setRectFill("");
    setRectX("0");
    setRectY("0");
    setShapeId(shapeId + 1);
  }

  // add Ellipse
  const handleSubmitEllipse = (event: ChangeEvent<HTMLFormElement>) => {
    event.preventDefault();
    if (!isNum(ellipseX) && !isNum(ellipseY)) {
      alert("Ellipse X and Y position must be valid integer number");
      return;
    }
    if (!isNum(ellipseRadiusX) && (isCircle || !isNum(ellipseRadiusY))) {
      alert("Ellipse radius must be valid integer number");
      return;
    }
    if (!isColor(ellipseFill)) {
      alert("color must be valid color, use #xxyyzz for hex color codes");
      return;
    }
    const id = "shapeId_" + shapeId
    shapeArr.push({
      id: id,
      shape: ShapeEnum.Ellipse,
      data: {
        x: parseInt(ellipseX) + parseInt(ellipseRadiusX),
        y: parseInt(ellipseY) + (isCircle ? parseInt(ellipseRadiusX) : parseInt(ellipseRadiusY)),
        radiusX: parseInt(ellipseRadiusX),
        radiusY: isCircle ? parseInt(ellipseRadiusX) : parseInt(ellipseRadiusY),
        fill: ellipseFill,
        id: id,
        isCircle: isCircle,
      }
    });
    setShapeArr(shapeArr);
    setEllipseRadiusX("");
    setEllipseRadiusY("");
    setEllipseFill("");
    setEllipseX("0");
    setEllipseY("0");
    setShapeId(shapeId + 1);
  }

  // add Polygon
  const handleSubmitPolygon = (event: ChangeEvent<HTMLFormElement>) => {
    event.preventDefault();
    if (!isNum(polygonX) && !isNum(polygonY)) {
      alert("Polygon X and Y position must be valid integer number");
      return;
    }
    if (!isNum(polygonRadius) && !isNum(polygonSides)) {
      alert("Polygon sides must be valid integer number");
      return;
    }
    if (!isColor(polygonFill)) {
      alert("color must be valid color, use #xxyyzz for hex color codes");
      return;
    }
    const id = "shapeId_" + shapeId
    shapeArr.push({
      id: id,
      shape: ShapeEnum.Polygon,
      data: {
        x: parseInt(polygonX) + parseInt(polygonRadius),
        y: parseInt(polygonY) + parseInt(polygonRadius),
        radius: parseInt(polygonRadius),
        sides: parseInt(polygonSides),
        fill: polygonFill,
        id: id,
      }
    });
    setShapeArr(shapeArr);
    setPolygonRadius("");
    setPolygonSides("");
    setPolygonFill("");
    setPolygonX("0");
    setPolygonY("0");
    setShapeId(shapeId + 1);
  }

  // add Label
  const handleSubmitLabel = (event: ChangeEvent<HTMLFormElement>) => {
    event.preventDefault();
    if (!isNum(labelX) && !isNum(labelY)) {
      alert("Label X and Y position must be valid integer number");
      return;
    }
    if (labelText === "") {
      alert("Label text must cannot be empty");
      return;
    }
    if (!isColor(labelFill)) {
      alert("Color must be valid color, use #xxyyzz for hex color codes");
      return;
    }

    const id = "shapeId_" + shapeId
    shapeArr.push({
      id: id,
      shape: ShapeEnum.Label,
      data: {
        x: parseInt(labelX),
        y: parseInt(labelY),
        text: labelText,
        fill: labelFill,
        id: id,
      }
    });
    setShapeArr(shapeArr);
    setLabelText("");
    setLabelFill("");
    setLabelX("0");
    setLabelY("0");
    setShapeId(shapeId + 1);
  }

  const handleSubmitChangeColor = (event: ChangeEvent<HTMLFormElement>) => {
    event.preventDefault();
    if (!isColor(selectedColor)) {
      alert("color must be valid color, use #xxyyzz for hex color codes");
      return;
    }

    shapeArr.map((shape, i) => {
      if (shape.id !== selectedId) {
        return null;
      }
      const shapes = shapeArr.slice();
      shape.data.fill = selectedColor;
      shapes[i] = shape;
      setShapeArr(shapes)
      return null;
    })
  }

  const handleBringToFront = () => {
    handleDeleteSelectedAndBringToFront("toFront");
  }
  const handleDeleteSelected = () => {
    handleDeleteSelectedAndBringToFront("");
  }

  const handleDeleteSelectedAndBringToFront = (type: string) => {
    const items = shapeArr.slice();
    const item = items.find((i) => i.id === selectedId);
    if (item !== undefined) {
      const index = items.indexOf(item);
      items.splice(index, 1);
      // add to the top
      if (type === "toFront") {
        items.push(item);
      }
      setShapeArr(items);
    }
  }

  // might be heavy to run, need test
  const handleSubmitChangeLabelText = (event: ChangeEvent<HTMLFormElement>) => {
    event.preventDefault();

    shapeArr.map((shape, i) => {
      if (shape.id !== selectedId) {
        return null;
      }
      if (isLabel(shape.data)) {
        const shapes = shapeArr.slice();
        shape.data.text = selectedLabelText;
        shapes[i] = shape;
        setShapeArr(shapes)
        return null;
      }
    })
  }

  const onChangeSelectShape = (shape: ShapeArrInterface) => {
    selectShape(shape.id)
    if (isLabel(shape.data)) {
      setSelectedLabelText(shape.data.text);
    }
  }

  return (
    <div>
      Select image &emsp;
      <input type="file" name="image" placeholder='Image' onChange={e => handleSetImage(e)} /><br />
      <div className='grid-container'>
        <form onSubmit={handleSubmitRect} className="grid-item">
          Rectangle <br />
          <label>
            <input
              type="checkbox"
              id="isSquare"
              name="isSquare"
              checked={isSquare}
              onChange={() => setIsSqure(!isSquare)}
            />
            is Square?
          </label>&emsp;
          <label>
            X position :
            <input type="text" name="rectX" value={rectX} onChange={(event: ChangeEvent<HTMLInputElement>) => {
              setRectX(event.target.value);
            }} />
          </label>&emsp;
          <label>
            Y position :
            <input type="text" name="rectY" value={rectY} onChange={(event: ChangeEvent<HTMLInputElement>) => {
              setRectY(event.target.value);
            }} />
          </label>&emsp;<br />
          <label>
            Width in Pixel :
            <input type="text" name="rectWidth" value={rectWidth} onChange={(event: ChangeEvent<HTMLInputElement>) => {
              setRectWidth(event.target.value);
            }} />
          </label>&emsp;
          {!isSquare &&
            <label>
              Height in Pixel :
              <input type="text" name="rectHeight" value={rectHeight} onChange={(event: ChangeEvent<HTMLInputElement>) => {
                setRectHeight(event.target.value);
              }} />&emsp;
            </label>
          }
          <label>
            Color :
            <input type="text" name="rectFill" value={rectFill} onChange={(event: ChangeEvent<HTMLInputElement>) => {
              setRectFill(event.target.value);
            }} />
          </label>&emsp;
          <input type="submit" value="Submit" />
        </form>
        <form onSubmit={handleSubmitEllipse} className="grid-item">
          Ellipse<br />
          <label>
            <input
              type="checkbox"
              id="isCircle"
              name="isCircle"
              checked={isCircle}
              onChange={() => setIsCircle(!isCircle)}
            />
            is Circle?
          </label>&emsp;
          <label>
            X position :
            <input type="text" name="ellipseX" value={ellipseX} onChange={(event: ChangeEvent<HTMLInputElement>) => {
              setEllipseX(event.target.value);
            }} />
          </label>&emsp;
          <label>
            Y position :
            <input type="text" name="ellipseY" value={ellipseY} onChange={(event: ChangeEvent<HTMLInputElement>) => {
              setEllipseY(event.target.value);
            }} />
          </label>&emsp;<br />
          <label>
            Radius X in Pixel :
            <input type="text" name="ellipseRadius" value={ellipseRadiusX} onChange={(event: ChangeEvent<HTMLInputElement>) => {
              setEllipseRadiusX(event.target.value);
            }} />
          </label>&emsp;
          {!isCircle &&
            <label>
              Radius Y in Pixel :
              <input type="text" name="ellipseRadius" value={ellipseRadiusY} onChange={(event: ChangeEvent<HTMLInputElement>) => {
                setEllipseRadiusY(event.target.value);
              }} />&emsp;
            </label>
          }
          <label>
            Color :
            <input type="text" name="ellipseFill" value={ellipseFill} onChange={(event: ChangeEvent<HTMLInputElement>) => {
              setEllipseFill(event.target.value);
            }} />
          </label>&emsp;
          <input type="submit" value="Submit" />
        </form>
        <form onSubmit={handleSubmitPolygon} className="grid-item">
          Polygon
          <div onChange={(event: any) => {
            setPolygonSides(event.target.value);
          }} >
            <input type="radio" value="3" checked={polygonSides === "3"} name="gender" /> Triangle
            <input type="radio" value="4" checked={polygonSides === "4"} name="gender" /> Square
            <input type="radio" value="128" checked={polygonSides === "128"} name="gender" /> Circle
          </div>
          <label>
            X position :
            <input type="text" name="polygonX" value={polygonX} onChange={(event: ChangeEvent<HTMLInputElement>) => {
              setPolygonX(event.target.value);
            }} />
          </label> &emsp;
          <label>
            Y position :
            <input type="text" name="polygonY" value={polygonY} onChange={(event: ChangeEvent<HTMLInputElement>) => {
              setPolygonY(event.target.value);
            }} />
          </label> &emsp;<br />
          <label>
            Radius in Pixel :
            <input type="text" name="polygonRadius" value={polygonRadius} onChange={(event: ChangeEvent<HTMLInputElement>) => {
              setPolygonRadius(event.target.value);
            }} />
          </label> &emsp;
          <label>
            Number of Sides :
            <input type="text" name="polygonRadius" value={polygonSides} onChange={(event: ChangeEvent<HTMLInputElement>) => {
              setPolygonSides(event.target.value);
            }} />&emsp;
          </label>
          <label>
            Color :
            <input type="text" name="polygonFill" value={polygonFill} onChange={(event: ChangeEvent<HTMLInputElement>) => {
              setPolygonFill(event.target.value);
            }} />
          </label> &emsp;
          <input type="submit" value="Submit" />
        </form >
        <form onSubmit={handleSubmitLabel} className="grid-item">
          Label<br />
          <label>
            X position :
            <input type="text" name="labelX" value={labelX} onChange={(event: ChangeEvent<HTMLInputElement>) => {
              setLabelX(event.target.value);
            }} />
          </label> &emsp;
          <label>
            Y position :
            <input type="text" name="labelY" value={labelY} onChange={(event: ChangeEvent<HTMLInputElement>) => {
              setLabelY(event.target.value);
            }} />
          </label> &emsp;<br />
          <label>
            Text
            <input type="text" name="labelText" value={labelText} onChange={(event: ChangeEvent<HTMLInputElement>) => {
              setLabelText(event.target.value);
            }} />
          </label> &emsp;
          <label>
            Color :
            <input type="text" name="labelFill" value={labelFill} onChange={(event: ChangeEvent<HTMLInputElement>) => {
              setLabelFill(event.target.value);
            }} /> *Color indicate Label background color, and inverted color for text color
          </label> &emsp;
          <input type="submit" value="Submit" />
        </form >
        <form onSubmit={handleSubmitChangeColor} className="grid-item">
          Selected Change Color<br />
          <label>
            Selected ID : {selectedId} &emsp;
            Color :
            <input type="text" name="selectedChangeColor" value={selectedColor} onChange={(event: ChangeEvent<HTMLInputElement>) => {
              selectColor(event.target.value);
            }} />
          </label>&emsp;
          <input type="submit" value="Submit" />
        </form>
        {selectedLabelText !== "" &&
          <form onSubmit={handleSubmitChangeLabelText} className="grid-item" >
            Change selected Label text<br />
            <label>
              Selected ID : {selectedId} &emsp;
              Text :
              <input type="text" name="selectedLabelText" value={selectedLabelText} onChange={(event: ChangeEvent<HTMLInputElement>) => {
                setSelectedLabelText(event.target.value);
              }} />
            </label>&emsp;
            <input type="submit" value="Submit" />
          </form>
        }
      </div>

      <button onClick={handleExport}>Download</button> &emsp;
      <button onClick={handleBringToFront}>Bring to Top</button> &emsp;
      <button onClick={handleDeleteSelected}>Delete Selected</button> &emsp;
      <Stage width={imgWidth} height={imgHeight} ref={stageRef}
        onMouseDown={checkDeselect}
        onTouchStart={checkDeselect}>
        <Layer>
          <MainCanvas image={new window.Image()} src={src} setWindowWidth={setImgWidth} />
          {shapeArr.map((shape, i) => {
            if (isRectangle(shape.data)) {
              return (
                <Rectangle
                  key={i}
                  shapeProps={shape.data}
                  isSelected={shape.id === selectedId}
                  onSelect={() => {
                    onChangeSelectShape(shape);
                  }}
                  onChange={(newAttrs: RectDataInterface) => {
                    const rects = shapeArr.slice();
                    rects[i].data = newAttrs;
                    setShapeArr(rects);
                  }}
                />
              );
            }
            if (isEllipse(shape.data)) {
              return (
                <EllipseC
                  key={i}
                  shapeProps={shape.data}
                  isSelected={shape.id === selectedId}
                  onSelect={() => {
                    onChangeSelectShape(shape);
                  }}
                  onChange={(newAttrs: EllipseDataInterface) => {
                    const ellipses = shapeArr.slice();
                    ellipses[i].data = newAttrs;
                    setShapeArr(ellipses);
                  }}
                />
              );
            }
            if (isPolygon(shape.data)) {
              return (
                <Polygon
                  key={i}
                  shapeProps={shape.data}
                  isSelected={shape.id === selectedId}
                  onSelect={() => {
                    onChangeSelectShape(shape);
                  }}
                  onChange={(newAttrs: PolygonDataInterface) => {
                    const polygons = shapeArr.slice();
                    polygons[i].data = newAttrs;
                    setShapeArr(polygons);
                  }}
                />
              );
            }
            if (isLabel(shape.data)) {
              return (
                <LabelC
                  key={i}
                  shapeProps={shape.data}
                  isSelected={shape.id === selectedId}
                  onSelect={() => {
                    onChangeSelectShape(shape);
                  }}
                  onChange={(newAttrs: LabelDataInterface) => {
                    const labels = shapeArr.slice();
                    labels[i].data = newAttrs;
                    setShapeArr(labels);
                  }}
                />
              );
            }
            return null;
          })}
        </Layer>
      </Stage>
    </div >
  );
}

export default App;
