import React, { FC } from 'react';
import { Transformer, Ellipse } from 'react-konva';
import Konva from 'konva';
import { EllipseInterface } from './Interface'

// converted from https://konvajs.org/docs/react/Transformer.html
const EllipseC: FC<EllipseInterface> = ({ shapeProps, isSelected, onSelect, onChange }) => {
    const shapeRef = React.useRef<Konva.Ellipse>(null)
    const trRef = React.useRef<Konva.Transformer>(null)

    React.useEffect(() => {
        if (isSelected) {
            // we need to attach transformer manually
            if (trRef !== null && trRef.current !== null && shapeRef !== null && shapeRef.current !== null) {
                trRef.current.nodes([shapeRef.current]);
                trRef.current.getLayer()?.batchDraw();
            }
        }
    }, [isSelected]);

    return (
        <React.Fragment>
            <Ellipse
                onClick={onSelect}
                onTap={onSelect}
                ref={shapeRef}
                {...shapeProps}
                draggable
                onDragEnd={(e) => {
                    onChange({
                        ...shapeProps,
                        x: e.target.x(),
                        y: e.target.y(),
                    });
                }}
                onTransformEnd={(e) => {
                    // transformer is changing scale of the node
                    // and NOT its width or height
                    // but in the store we have only width and height
                    // to match the data better we will reset scale on transform end
                    const node = shapeRef.current;
                    if (node === null) {
                        return;
                    }
                    let scaleX = node.scaleX();
                    let scaleY = node.scaleY();
                    // we will reset it back
                    node.scaleX(1);
                    node.scaleY(1);

                    let width = node.width() * scaleX;
                    let height = node.height() * scaleY;

                    if (shapeProps.isCircle) {
                        if (scaleX.toFixed(5) === scaleY.toFixed(5)) {
                            scaleX = Math.max(scaleX, scaleY);
                            scaleY = scaleX;
                        } else {
                            if (scaleX.toFixed(5) !== "1.00000") {
                                height = width;
                            } else {
                                width = height;
                            }
                        };
                    }

                    onChange(
                        {
                            ...shapeProps,
                            x: node.x(),
                            y: node.y(),
                            // set minimal value
                            width: height,
                            height: width,
                        });
                }}
            />
            {isSelected && (
                <Transformer
                    ref={trRef}
                    boundBoxFunc={(oldBox, newBox) => {
                        // limit resize
                        if (newBox.width < 5 || newBox.height < 5) {
                            return oldBox;
                        }
                        return newBox;
                    }}
                />
            )}
        </React.Fragment>
    );
};

export default EllipseC;